.App {
  background-color: #1d1b1c;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: whitesmoke;
  &__header {
    height: fit-content;
    &__logo {
      width: 60%;
      margin-top: 36px;
    }
  }
  &__main {
    flex-direction: column;
    text-align: center;
    align-items: center;
  }
}
